*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
body{
    background-color: rgb(34, 32, 32);
}
.hero{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 80px;
    display: none;
   

}
.box{
    width: 550px;
    padding: 20px;
    border: 1px solid #51cc51;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
}
.header{
    width: 100%;
    text-align: center;
    color: white;
    font-size: 2rem;
    font-weight: bolder;
    padding: 5px;
    border: 3px solid #51cc51;
}
.instruction{
    width: 100%;
    display: flex;
    align-items: center;
    padding: 5px;
}
.instruction > ul{
    list-style: none;
}
.instruction > ul > li{
    color: silver;
    font-size: 1rem;
padding: 5px;    
}
.inp-user{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
}
.inp-user > input{
    width: 280px;
    padding: 10px 10px;
    font-size: 0.8rem;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #51cc51;
    text-align: center;
    color: white;
}
.inp-user > input::placeholder{
    color: #e0e0e0;
}
.inp-user > input:focus{
    outline: none;
}
.startBtn{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
}
.startBtn > button{
    padding: 6px 12px;
    text-align: center;
    border: none;
    background-color: yellow;
    color: black;
    font-size: 1rem;
}
.QuizSec{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 80px;
    /* display: none; */
}
.box{
    width: 550px;
    padding: 20px;
    border: 1px solid #51cc51;
    display: flex;
    align-items: center;
    justify-content: center;
