}
.que-time{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.que{
   width: 75%;
    display: flex;
    align-items: center;
    font-size: 1rem;
    font-weight: bolder;
    color: white;
}
.timer{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 2px solid #51cc51;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    font-weight: bolder;
    padding: 5px;
    }
.opt{
    width: 100%;
    display: flex;
    align-items: center;
    color:silver;
    gap: 10px;
    cursor: pointer;
    font-size: 0.9rem;
}

.opt > input[type="radio"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid white;
    
    border-radius: 50%;
    position: relative;
    cursor: pointer;
}

.opt > input[type="radio"]:checked::before {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    background-color: #51cc51;
    border-radius: 50%;
   
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.opt > input[type="radio"]:focus {
    background-color: #51cc51;
    border: #51cc51;
    outline: none;
}

